@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Sans+KR:wght@300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Ubuntu+Mono&display=swap');

html,
body,
.root {
  width: 100vw;
  height: 100vh;
}

body {
  height: 100%;
  font-family: 'IBM Plex Sans KR', system-ui, -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
    sans-serif;
  font-size: 16px;
  line-height: 160%;
  display: flex;
  flex-direction: column;
}

pre,
code {
  font-family: 'Ubuntu Mono', monospace;
}

.link:hover {
  background-color: #252730 !important;
}
